.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
  position: relative;
  text-align: center;
  color: #ffffff;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: all 0.3s ease;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap h3 {
  font-size: clamp(1.375rem, 1.375rem + 0.5rem * (100vw - 23.4375rem) / 101.0625rem, 1.875rem);
  font-weight: 700;
  margin-bottom: 10px;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap .icon {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s ease;
  margin-bottom: 10px;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap .short {
  font-size: clamp(0.6875rem, 0.6875rem + 0.25rem * (100vw - 23.4375rem) / 101.0625rem, 0.9375rem);
  font-weight: 700;
  line-height: clamp(0.875rem, 0.875rem + 0.375rem * (100vw - 23.4375rem) / 101.0625rem, 1.25rem);
  opacity: 1;
  transform: translateY(0);
  transition: all 0.3s ease;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap .long {
  font-size: clamp(0.6875rem, 0.6875rem + 0.25rem * (100vw - 23.4375rem) / 101.0625rem, 0.9375rem);
  font-weight: 400;
  line-height: clamp(0.875rem, 0.875rem + 0.375rem * (100vw - 23.4375rem) / 101.0625rem, 1.25rem);
  opacity: 0;
  transform: translateY(10px);
  height: 0;
  transition: all 0.3s ease;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card .card-content .content-wrap .btn {
  opacity: 0;
  transform: translateY(10px);
  margin-top: 10px;
  transition: all 0.3s ease;
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card:hover .content-wrap .icon {
  opacity: 1;
  transform: translateY(0);
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card:hover .content-wrap .long {
  opacity: 1;
  height: auto;
  transform: translateY(0);
}
.fullwidth-block-hover .carousel.slick-initialized .slick-list .slick-track .slick-slide .card:hover .content-wrap .btn {
  opacity: 1;
  transform: translateY(0);
}

